<template>
  <el-dialog
    :visible="visible"
    @update:visible="$emit('update:visible', $event)"
    append-to-body
    title=""
    v-if="visible"
    width="600px"
  >
    <!--  -->
    <el-dialog width="90%" :visible.sync="isShowPreview" append-to-body>
      <el-card header="" shadow="hover" class="box-card">
        <ele-form
          :form-desc="formDesc"
          v-model="formData"
          :request-fn="handleRequest"
          @request-success="handleRequestSuccess"
          v-bind="formAttr"
        ></ele-form>
      </el-card>
      <div slot="footer">
        <el-button @click="isShowPreview = false"></el-button>
        <el-button type="primary" @click="confirmGen"></el-button>
      </div>
    </el-dialog>

    <el-alert
      title="JSON"
      style="margin-bottom: 20px"
      type="warning"
      show-icon
    >
    </el-alert>

    <!--  -->
    <codemirror @input="handleChange" v-model="jsonStr"></codemirror>
    <!--  -->
    <div style="text-align: center;margin-top: 20px">
      <el-button @click="handleImport" :disabled="importDisabled" type="primary"
        ></el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import _ from "lodash";
import formAttrDefault from "@/store/formAttrDefault";

export default {
  name: "importDialog",
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    importDisabled() {
      return !this.jsonData || Object.keys(this.jsonData).length === 0;
    }
  },
  watch: {
    // , 
    visible(val) {
      if (!val) {
        this.isShowPreview = false;
        this.jsonData = {};
        this.formDesc = {};
        this.jsonStr = "{}";
      }
    }
  },
  data() {
    return {
      isShowPreview: false,
      formDesc: {},
      formAttr: {},
      jsonData: {},
      jsonStr: "{}",
      isError: false,
      formData: {}
    };
  },
  methods: {
    // 
    handleChange() {
      if (!this.handleChangeFn) {
        this.handleChangeFn = _.debounce(() => {
          try {
            const json = eval("(" + this.jsonStr + ")");
            if (typeof json === "object") {
              this.jsonData = _.cloneDeep(json);
              this.formDesc = this.jsonData.formDesc;
              this.isError = false;
            } else {
              this.isError = true;
            }
          } catch (err) {
            this.isError = true;
          }
        }, 1000);
      }
      this.handleChangeFn();
    },
    // json
    handleImport() {
      if (typeof this.jsonData !== "object" || this.isError) {
        this.$message.error(" JSON ");
        return false;
      }

      if (this.jsonData && this.jsonData.formDesc) {
        const keys = Object.keys(formAttrDefault);
        this.formAttr = Object.assign(
          {},
          formAttrDefault,
          _.pick(this.jsonData, keys)
        );

        //  formAttr
        this.isShowPreview = true;
      } else {
        this.$message.error(' "formDesc" ');
        return false;
      }
    },
    // jsonjson  list)
    json2Form() {
      // list 
      this.$store.commit("clearCurrentForm");
      // 1. formAttr 
      this.$store.commit("updateCurrentFormAttr", this.formAttr);
      // 2. formDesc
      // 3. formDesc.attr
      const list = Object.entries(this.formDesc).map(([key, val]) => ({
        ...val,
        attrs: val.attrs || {},
        field: key
      }));
      //  list
      this.$store.commit("updateCurrentFormItemList", list);
      //  selectIndex
      this.$store.commit("updateFormItemIndex", 0);
    },

    // 
    confirmGen() {
      this.json2Form();
      this.$emit("update:visible", false);
    },
    handleRequest(data) {
      // eslint-disable-next-line no-console
      console.log(data);
      return Promise.resolve();
    },
    handleRequestSuccess() {
      this.$message.success("");
    }
  }
};
</script>
