<template>
  <ele-form-dialog
    v-model="formData"
    :formDesc="batchFormDesc"
    labelPosition="left"
    width="800px"
    :dialogAttrs="{
      'close-on-click-modal': false,
      'append-to-body': true
    }"
    :request-fn="handleAdd"
    :visible="visible"
    @update:visible="$emit('update:visible', $event)"
    title=""
  ></ele-form-dialog>
</template>

<script>
import _ from "lodash";
import { addFormItem } from "@/helpers/tool";
import { mapGetters } from "vuex";

export default {
  name: "batchDialog",
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters(["currentFormDesc", "currentFormItemList", "sortedComps"]),
    countObj() {
      return _.countBy(this.formData.data, o => o.field);
    },
    batchFormDesc() {
      return {
        type: {
          type: "radio",
          label: "",
          default: "dynamic",
          options: [
            { text: "", value: "dynamic" },
            { text: "", value: "json-editor" },
            {
              text: "()",
              value: "mysql",
              attrs: { disabled: true }
            }
          ]
        },
        data: {
          type: data => data.type,
          label: "",
          isTypeChangeReloadValue: false,
          attrs: {
            columns: [
              {
                type: "el-input",
                valueKey: "field",
                attrs: {
                  placeholder: " field"
                }
              },
              {
                type: "el-input",
                valueKey: "label",
                attrs: {
                  placeholder: " label"
                }
              },
              {
                type: "el-select",
                valueKey: "type",
                slots: {
                  default: h => {
                    return this.sortedComps.map(item =>
                      h("el-option", {
                        attrs: {
                          label: `${item.type} - ${item.label}`,
                          value: item.type
                        }
                      })
                    );
                  }
                },
                attrs: {
                  filterable: true,
                  placeholder: " type"
                }
              }
            ]
          },
          valueFormatter: arr =>
            arr.map(item => _.pick(item, ["field", "label", "type"])),
          rules: {
            type: "array",
            validator: (rule, value, callback) => {
              let errorArr = [];
              if (Array.isArray(value) && value.length) {
                // 
                errorArr = value
                  .map((item, index) =>
                    this.currentFormDesc[item.field] ||
                    this.countObj[item.field] > 1
                      ? `${index + 1} field `
                      : null
                  )
                  .filter(Boolean);

                // 
                errorArr = errorArr.concat(
                  value
                    .map((item, index) => {
                      const errMsg = [];
                      if (!item.field) errMsg.push("field");
                      if (!item.label) errMsg.push("label");
                      if (!item.type) errMsg.push("type");

                      return errMsg.length
                        ? `${index + 1} ${errMsg.join("")} `
                        : null;
                    })
                    .filter(Boolean)
                );
              }
              errorArr.length
                ? callback(new Error(errorArr.join(" & ")))
                : callback();
            }
          }
        }
      };
    }
  },
  data() {
    return {
      formData: {
        type: "dynamic",
        data: []
      }
    };
  },
  methods: {
    updateList(data) {
      this.$store.commit("updateCurrentFormItemList", data);
    },

    handleAdd({ data }) {
      if (Array.isArray(data) && data.length) {
        const newFormItems = data.map(item =>
          addFormItem(item.type, { field: item.field, label: item.label })
        );
        this.updateList(this.currentFormItemList.concat(newFormItems));
        this.$message.success("");
        this.formData = {
          type: "dynamic",
          data: []
        };

        // type
        newFormItems.forEach(item =>
          this.$store.commit("updateCompCount", item.type)
        );

        this.$emit("update:visible", false);
      }
    }
  }
};
</script>
