import "./extend";
import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import ElementUI from "element-ui";
import EleForm from "vue-ele-form";
import codemirrorConfig from "./extend/codemirror";
import "@/helpers/api";
// css
import "normalize.css";
import "element-ui/lib/theme-chalk/index.css";

// 
Vue.use(EleForm, {
  upload: {
    action: "https://www.mocky.io/v2/5cc8019d300000980a055e76",
    responseFn(response) {
      //  mock , , URL, 
      return response.url;
    }
  },
  "upload-file": {
    responseFn(response, file) {
      return {
        name: file.name,
        url: URL.createObjectURL(file.raw),
        size: file.size
      };
    }
  },
  codemirror: codemirrorConfig,
  bmap: {
    ak: "9YLHZRPvUNLhi34Oh2ojqeGSpzCf1rVG"
  }
});
Vue.use(ElementUI);

// 
Vue.config.productionTip = false;

// 
const appVueOptions = {
  store,
  render: h => h(App)
};

const isLab = process.env.VUE_APP_IS_LAB;
if (!isLab) {
  new Vue(appVueOptions).$mount("#app");
}

// Vue
export default appVueOptions;
