<template>
  <el-dialog
    :visible="visible"
    append-to-body
    title=""
    v-if="visible"
    @update:visible="$emit('update:visible', $event)"
    width="600px"
  >
    <codemirror :value="codeHtml"></codemirror>
    <div style="text-align: center;margin-top: 20px">
      <el-button @click="handleCopyHtml" type="primary"></el-button>
      <el-button type="primary"
        ><el-link
          :href="fileURL"
          style="color: white;"
          :underline="false"
          :download="currentForm.name + '.vue'"
          ></el-link
        ></el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import tpl from "./tpl.ejs";
import copy from "clipboard-copy";
import _ from "lodash";
import serialize from "serialize-javascript";
import { mapGetters } from "vuex";
const ejs = require("ejs");

export default {
  name: "htmlDialog",
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    formDesc: {
      type: Object,
      default: () => ({})
    },
    formAttr: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    ...mapGetters(["currentForm"]),
    // 
    codeHtml() {
      //   formDesc  
      const getFormDescStr = formDesc => {
        if (_.isEmpty(formDesc)) return "{}";

        return (
          serialize(formDesc, { space: 2 })
            // , {\n   'name': 'zhang'} -> {\n\t  'name': 'zhang'} 
            .replace(/\n/g, "\n\t  ")
            //  key : '{"name": "zhang"}' -> '{name: "zhang"}'
            .replace(/"(\w+)":/g, "$1:")
        );
      };

      const getFormAttrObj = formAttr => {
        return Object.entries(formAttr).map(([key, value]) => {
          //  [['name', 'zhang'], ['age', 10]] => [{name: 'zhang', ':age': 10}]
          //  vue   : 
          key = typeof value === "string" ? key : `:${key}`;
          return {
            key,
            value
          };
        });
      };
      return ejs.render(tpl, {
        formAttr: getFormAttrObj(this.formAttr),
        formDesc: getFormDescStr(this.formDesc)
      });
    },
    fileURL() {
      const blob = new Blob([this.codeHtml]);
      return URL.createObjectURL(blob);
    }
  },
  methods: {
    handleCopyHtml() {
      copy(this.codeHtml);
      this.$message.success("!");
    }
  }
};
</script>
